.item1 {
  grid-area: header;
}
.item2 {
  grid-area: menu;
}
.item3 {
  grid-area: main;
}
.item4 {
  grid-area: right;
}
.item5 {
  grid-area: footer;
}

.grid-container {
  height: 100%;
  display: grid;
  grid-template-areas:
    "header header header header header header"
    "main main main main main main"
    "main main main main main main"
    "footer footer footer footer footer footer";
  gap: 10px;
  /* background-color: #2196f3; */
  /* padding: 10px; */
}

